.container-fluid,
.container {
    max-width: 1200px;
}

.card-container {
    padding: 100px 0px;
    -webkit-perspective: 1000;
    perspective: 1000;
}



.profile-card-4 {
    max-width: 300px;
    
    background-color: #FFF;
    border-radius: 5px;
    box-shadow: 20px 20px 25px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    position: -ms-page;
    margin: 10px auto;
    cursor: pointer;
}

.profile-card-4 img {
    transition: all 0.25s linear;
}

.profile-card-4 .profile-content {
    position: relative;
    padding: 15px;
    background-color: #FFF;
}

.profile-card-4 .profile-name {
    font-weight: bold;
    position: absolute;
    left: 0px;
    right: 0px;
    top: -70px;
    color: #FFF;
    font-size: 17px;
    background-color: rgb(0, 0, 0);
}

.profile-card-4 .profile-name p {
    font-weight: 900;
    font-size: 16px;
    letter-spacing: 1.5px;
}

.profile-card-4 .profile-description {
    color: #777;
    font-size: 20px;
    padding: 10px;
}

.profile-card-4 .profile-overview {
    padding: 15px 0px;
    font-size: 20px;
}

.profile-card-4 .profile-overview p {
    font-size: 20px;
    font-weight: 600;
    color: #777;
}

.profile-card-4 .profile-overview h4 {
    color: #273751;
    font-weight: bold;
}

.profile-card-4 .profile-content::before {
    content: "";
    position: absolute;
    height: 20px;
    top: -10px;
    left: 0px;
    right: 0px;
    background-color: #FFF;
    z-index: 0;
    -webkit-transform: skewY(3deg);
            transform: skewY(3deg);
}

.profile-card-4:hover img {
    -webkit-transform: rotate(5deg) scale(1.1, 1.1);
            transform: rotate(5deg) scale(1.1, 1.1);
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}






